import React, { useContext, useState, useEffect } from "react";
import { GameContext } from "./GameContext";
import { useNavigate } from "react-router-dom";

// Иконки (оставлены только необходимые для рулетки и навигации)
import RouleteIcon from "./assets/icons/Roulete.png";
import SpinIcon from "./assets/icons/spin.png";
import upgradesIcon from "./assets/icons/click.png";
import shopIcon from "./assets/icons/shop.png";
import settingsIcon from "./assets/icons/settings.png";

const Upgrades = () => {
  const { state, dispatch } = useContext(GameContext);
  const navigate = useNavigate();

  const [countdown, setCountdown] = useState(0);

  const formatTime = (ms) => {
    const totalSec = Math.floor(ms / 1000);
    const hours = Math.floor(totalSec / 3600);
    const minutes = Math.floor((totalSec % 3600) / 60);
    const seconds = totalSec % 60;
    return `${hours.toString().padStart(2, "0")}:${minutes
      .toString()
      .padStart(2, "0")}:${seconds.toString().padStart(2, "0")}`;
  };

  useEffect(() => {
    const tick = () => {
      if (state.lastRouletteSpin) {
        const cooldown = 10800000;
        const elapsed = Date.now() - state.lastRouletteSpin;
        const remaining = cooldown - elapsed;
        setCountdown(remaining > 0 ? remaining : 0);
      } else {
        setCountdown(0);
      }
    };
    tick();
    const interval = setInterval(tick, 1000);
    return () => clearInterval(interval);
  }, [state.lastRouletteSpin]);

  const handleStartRoulette = () => {
    dispatch({ type: "START_ROULETTE" });
    setTimeout(() => {
      const bonus = Math.floor(Math.random() * (2000 - 50 + 1)) + 50;
      dispatch({ type: "FINISH_ROULETTE", payload: { bonus } });
    }, 3000);
  };

  const handleClearRoulette = () => {
    dispatch({ type: "CLEAR_ROULETTE" });
  };

  let rouletteButtonText = "";
  let rouletteDisabled = false;
  if (state.plums < 200) {
    rouletteButtonText = "Недостаточно PC";
    rouletteDisabled = true;
  } else if (state.rouletteSpinsToday >= 3) {
    rouletteButtonText = "Максимум 3 раза в день";
    rouletteDisabled = true;
  } else if (countdown > 0) {
    rouletteButtonText = `Подождите ${formatTime(countdown)}`;
    rouletteDisabled = true;
  } else {
    rouletteButtonText = "Крутануть Рулетку";
    rouletteDisabled = false;
  }

  const headingClass = "font-extrabold text-white text-[clamp(1rem,1.5vw,1.5rem)]";
  const paragraphClass = "text-[clamp(0.75rem,1.5vw,1rem)] text-white";
  const buttonStyle =
    "w-full py-2 rounded-xl bg-blue-700 text-[clamp(0.75rem,1.5vw,1rem)] font-bold hover:bg-blue-800 transition duration-200 shadow-md";
  const cardStyle =
    "rounded-2xl p-4 sm:p-6 md:p-8 shadow-xl hover:shadow-2xl transition-transform transform hover:scale-105 bg-gradient-to-br from-yellow-300/20 to-yellow-500/20 border border-yellow-300/20 backdrop-blur-sm";

  return (
    <div className="min-h-screen overflow-y-auto from-blue-500 via-purple-500 to-pink-500 text-white p-4 relative">
      <div className="container mx-auto max-w-4xl">
        <h1 className="text-center mb-12 text-white drop-shadow-lg text-[clamp(2rem,4vw,3rem)] font-extrabold">
          АПГРЕЙДЫ
        </h1>

         {/*Сетка карточек:*/}	
        <div className="grid grid-cols-2 gap-4 md:gap-6">
	
{/*Авто Сбор */}
<div className={cardStyle}>
  <div className="flex items-center mb-4">
    <img
      src={require('./assets/icons/autotap.png')}
      alt="Авто сбор"
      className="w-10 sm:w-12 h-10 sm:h-12 mr-3"
    />
    <h2 className={headingClass}>Авто сбор</h2>
  </div>
  {!state.autoCollectorPurchased ? (
    <>
      <p className={`mb-2 ${paragraphClass}`}>
        Приобретите возможность собирать Plum Coin автоматически.
      </p>
      <p className={`mb-4 ${paragraphClass}`}>
        Стоимость: <b>10 GP</b>
      </p>
      <button
        onClick={() => {
          if (state.gp >= 10) {
            dispatch({
              type: "BUY_AUTO_COLLECTOR",
              payload: { cost: 10 },
            });
          } else {
            alert("Недостаточно GP!");
          }
        }}
        className={buttonStyle}
      >
        Купить
      </button>
    </>
  ) : (
    <>
      <p className={`mb-2 ${paragraphClass}`}>
        Уровень авто сбора: <b>{state.autoCollectorLevel}</b>
      </p>
      <p className={`mb-4 ${paragraphClass}`}>
        Стоимость улучшения: <b>{state.autoCollectorUpgradeCost} PC</b>
      </p>
      <button
        onClick={() => {
          if (state.plums >= state.autoCollectorUpgradeCost) {
            dispatch({ type: "UPGRADE_AUTO_COLLECTOR" });
          } else {
            alert("Недостаточно PC!");
          }
        }}
        className={buttonStyle}
      >
        Улучшить
      </button>
    </>
  )}
</div>
	
{/*Апгрейд 5 пальцев */}
<div className={cardStyle}>
  <div className="flex items-center mb-4">
    <img
      src={require('./assets/icons/finger.png')}
      alt="5 пальцев"
      className="w-10 sm:w-12 h-10 sm:h-12 mr-3"
    />
    <h2 className={headingClass}>5 Пальцев</h2>
  </div>
  <p className={`mb-2 ${paragraphClass}`}>
    Позволяет собирать x1–x5 Plum Coin в зависимости от количества пальцев.
  </p>
  <p className={`mb-4 ${paragraphClass}`}>
    Стоимость: <b>50 GP</b>
  </p>
  {!state.fingerUpgradePurchased ? (
    <button
      onClick={() => {
        if (state.gp >= 50) {
          dispatch({
            type: "BUY_FINGER_UPGRADE",
            payload: { cost: 50 },
          });
        } else {
          alert("Недостаточно GP!");
        }
      }}
      className={buttonStyle}
    >
      Купить
    </button>
  ) : (
    <span className="text-green-400 font-bold">Куплено</span>
  )}
</div>
		{/*Рулетка */}
          <div className={cardStyle}>
            <div className="flex items-center mb-4">
              <img
                src={RouleteIcon}
                alt="Roulette"
                className="w-10 sm:w-12 h-10 sm:h-12 mr-3"
              />
              <h2 className={headingClass}>Рулетка</h2>
            </div>
            <p className={`mb-4 ${paragraphClass}`}>
              Стоимость прокрута: <b>500 PC</b>
            </p>
            {state.rouletteSpinning ? (
              <div className="flex items-center justify-center">
                <img
                  src={SpinIcon}
                  alt="spin"
                  className="w-10 sm:w-12 h-10 sm:h-12 animate-spin"
                />
                <span className={`ml-3 ${paragraphClass}`}>Вращается…</span>
              </div>
            ) : state.rouletteResult !== null ? (
              <div className="text-center">
                <p className={`mb-4 ${paragraphClass}`}>
                  Вы выиграли: <b>{state.rouletteResult} PC</b>!
                </p>
                <button onClick={handleClearRoulette} className={buttonStyle}>
                  Принять
                </button>
              </div>
            ) : (
              <button
                onClick={handleStartRoulette}
                className={`${buttonStyle} py-3`}
                disabled={rouletteDisabled}
              >
                {rouletteButtonText}
              </button>
            )}	
          </div>
        </div>
		
      </div>

      {/* Фиксированная кнопка возврата (как в Leaderboard) */}
      {/* Назад */}
      <button className="back-button" onClick={() => navigate(-1)}>
        ←
      </button>
    </div>
  );
};

export default Upgrades;
