// src/Shop.js
import React, { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { GameContext } from "./GameContext";
import { supabase } from "./supabaseClient";
import "./Shop.css";
import GPIcon from "./assets/Shop/GP.png";
import PrimeIcon from "./assets/Shop/Prime.png"; // PRIME иконка
import PremiumChestIcon from "./assets/Shop/PremiumChest.png"; // Иконка для премиум сундука
import { motion, AnimatePresence } from "framer-motion";

const paidProducts = [
  {
    id: "prime1",
    name: "Prime Статус",
    price: 1000,
    currency: "gp",
    description: "Доступ к PRIME-функциям и статусу",
    grantPrime: true,
  },
  {
    id: "p5",
    name: "Сундук GP",
    price: 100000,
    currency: "plum",
    gpAmount: 10,
    description: "Пополнение на 10 GP",
  },
  {
    id: "p6",
    name: "Сундук GP Premium",
    price: 500000,
    currency: "plum",
    gpAmount: 50,
    description: "Пополнение на 50 GP",
  },
  // Дополнительные товары можно добавлять сюда
];

const Shop = () => {
  const navigate = useNavigate();
  const { state, dispatch } = useContext(GameContext);
  
  const [showPurchaseModal, setShowPurchaseModal] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [purchaseEffect, setPurchaseEffect] = useState(false);

  // Сортировка: товары с grantPrime идут первыми
  const sortedProducts = [
    ...paidProducts.filter((prod) => prod.grantPrime),
    ...paidProducts.filter((prod) => !prod.grantPrime),
  ];

  const handlePurchaseButtonClick = (product) => {
    setSelectedProduct(product);
    setShowPurchaseModal(true);
  };

  const handleConfirmPurchase = async () => {
    const price = selectedProduct.price;

    if (selectedProduct.grantPrime) {
      if (!state.isPrime && state.gp >= price) {
        dispatch({ type: "BUY_PRIME", payload: { cost: price } });
        // Обновление Supabase для Prime
        const {
          data: { user },
          error: userError,
        } = await supabase.auth.getUser();
        if (user) {
          const { error } = await supabase
            .from("user_stats")
            .update({ is_prime: true })
            .eq("user_id", user.id);
          if (error) {
            console.error("Ошибка при обновлении Prime в Supabase:", error);
          }
        } else {
          console.error("Ошибка получения пользователя:", userError);
        }
        alert("Вы успешно приобрели PRIME!");
      } else {
        alert("У вас уже есть Prime или недостаточно GP.");
      }
    } else if (selectedProduct.currency === "plum") {
      if (state.plums >= price) {
        dispatch({
          type: "BUY_GP",
          payload: { amount: selectedProduct.gpAmount, cost: price },
        });
        alert(`Вы получили ${selectedProduct.gpAmount} GP.`);
      } else {
        alert("Недостаточно PC для покупки.");
      }
    }



    // Запуск эффекта покупки (эффект взрыва)
    setPurchaseEffect(true);
    setTimeout(() => {
      setPurchaseEffect(false);
    }, 1000);

    setShowPurchaseModal(false);
    setSelectedProduct(null);
  };

  const handleCancelPurchase = () => {
    setShowPurchaseModal(false);
    setSelectedProduct(null);
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center from-gray-800 to-blue-900 text-white p-4 md:p-6 relative">
      <h2 className="text-4xl sm:text-6xl font-bold text-white mb-8 drop-shadow-lg text-center">
        МАГАЗИН
      </h2>

      {/* Сетка товаров: две колонки. Новые товары автоматически переносятся в следующие ряды */}
      <div className="max-w-screen-lg w-full mx-auto grid grid-cols-2 gap-4 px-2">
        {sortedProducts.map((product) => (
          <div
            key={product.id}
            className="vitrine-container min-h-[300px] bg-white bg-opacity-20 backdrop-blur-xl rounded-2xl shadow-2xl p-4 flex flex-col items-center transform hover:scale-105 transition duration-300"
          >
            <img
              src={
                product.id === "p6"
                  ? PremiumChestIcon
                  : product.grantPrime
                  ? PrimeIcon
                  : GPIcon
              }
              alt={product.name}
              className="w-24 h-24 object-contain mb-4 rounded-lg"
            />
            <h3 className="text-base sm:text-lg font-bold mb-1 text-center">
              {product.name}
            </h3>
            <p className="text-xs sm:text-sm mb-2 text-center">
              {product.description}
            </p>
            <p className="text-sm sm:text-base font-bold mb-4">
              {product.price.toLocaleString()}{" "}
              {product.currency === "plum" ? "PC" : "GP"}
            </p>
            {/* mt-auto гарантирует, что все кнопки "Купить" будут выровнены по нижней части карточки */}
            <button
              onClick={() => handlePurchaseButtonClick(product)}
              className="w-full py-2 rounded-full bg-blue-600 hover:bg-blue-700 transition duration-200 font-bold text-white shadow-md transform hover:scale-105 mt-auto"
            >
              Купить
            </button>
          </div>
        ))}
      </div>

      {/* Фиксированная кнопка "назад" */}
      {/* Назад */}
      <button className="back-button" onClick={() => navigate(-1)}>
        ←
      </button>

      {/* Модальное окно подтверждения покупки */}
      {showPurchaseModal && selectedProduct && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-80 backdrop-blur-md">
          <div className="relative bg-gradient-to-br from-purple-800 to-pink-800 rounded-3xl p-6 sm:p-8 w-11/12 max-w-md shadow-2xl">
            <button
              onClick={handleCancelPurchase}
              aria-label="Закрыть"
              className="absolute top-4 right-4 text-red-400 text-3xl sm:text-4xl font-bold hover:text-red-600 transition"
            >
              &times;
            </button>
            <h2 className="text-2xl sm:text-3xl font-extrabold mb-4 sm:mb-6 text-yellow-300 drop-shadow-md">
              Подтверждение покупки
            </h2>
            <p className="text-base sm:text-lg text-white">
              Вы уверены, что хотите купить "{selectedProduct.name}" за{" "}
              {selectedProduct.price.toLocaleString()}{" "}
              {selectedProduct.currency === "plum" ? "PC" : "GP"}?
              <br />
              <span className="mt-2 block text-sm sm:text-base italic">
                {selectedProduct.description}
              </span>
            </p>
            <div className="flex justify-end gap-4 sm:gap-6 mt-6">
              <button
                onClick={handleConfirmPurchase}
                className="px-4 py-2 sm:px-6 sm:py-3 bg-green-500 hover:bg-green-600 text-white font-bold rounded-xl transition-transform transform hover:scale-105"
              >
                Купить
              </button>
              <button
                onClick={handleCancelPurchase}
                className="px-4 py-2 sm:px-6 sm:py-3 bg-gray-300 hover:bg-gray-400 text-black font-bold rounded-xl transition-transform transform hover:scale-105"
              >
                Отмена
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Эффект покупки: эффект "взрыва" */}
      <AnimatePresence>
        {purchaseEffect && (
          <motion.div
            initial={{ scale: 0, opacity: 1, rotate: 0 }}
            animate={{ scale: 2.5, opacity: 0, rotate: 360 }}
            exit={{ opacity: 0 }}
            transition={{ duration: 1, ease: "easeOut" }}
            className="absolute inset-0 flex items-center justify-center pointer-events-none"
          >
            <div className="w-32 h-32 bg-gradient-to-r from-yellow-400 to-red-600 rounded-full"></div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default Shop;
