import React, { useState, useEffect } from "react";
import { supabase } from "./supabaseClient";
import { useNavigate } from "react-router-dom";
import PrimeIcon from "./assets/PRIME/PRIME.png";
import "./Leaderboard.css";

function Leaderboard() {
  const [players, setPlayers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [sessionUser, setSessionUser] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    async function fetchUser() {
      const { data: { user }, error } = await supabase.auth.getUser();
      if (!error) setSessionUser(user);
    }
    fetchUser();
  }, []);

  const fetchPlayers = async (withLoading = false) => {
    if (withLoading) setLoading(true);
    const { data, error } = await supabase
      .from("user_stats")
      .select(
        "user_id, level, plum_count, is_prime, updated_at, users(username)"
      )
      .order("plum_count", { ascending: false });
    if (!error) setPlayers(data);
    if (withLoading) setLoading(false);
  };

  useEffect(() => {
    fetchPlayers(true);
    const interval = setInterval(() => fetchPlayers(false), 10000);
    return () => clearInterval(interval);
  }, []);

  if (loading) {
    return (
      <div className="text-white text-center mt-20">
        Загрузка таблицы лидеров...
      </div>
    );
  }

  const currentUserIndex = sessionUser
    ? players.findIndex((player) => player.user_id === sessionUser.id)
    : -1;
  const topPlayers = players.slice(0, 3);
  const lastPlayer = players[players.length - 1];
  const currentUser = players[currentUserIndex];

  return (
    <div className="leaderboard-container relative min-h-screen text-white">
      <h1 className="leaderboard-title text-center mb-12 drop-shadow-lg text-[clamp(2rem,4vw,3rem)] font-extrabold">
        ЛИДЕРБОРД
      </h1>

      {/* Подиум */}
      <div className="podium flex flex-col items-center justify-center">
        {/* 1 место сверху */}
        {topPlayers[0] && (
          <PlayerCard
            player={topPlayers[0]}
            place={1}
            size="large"
            animation="pulse"
          />
        )}

        {/* 2 и 3 место снизу */}
        <div className="lower-podium flex gap-4 justify-center items-end mt-4">
          {topPlayers[1] && (
            <PlayerCard
              player={topPlayers[1]}
              place={2}
              size="large"
              animation="slide-left"
            />
          )}
          {topPlayers[2] && (
            <PlayerCard
              player={topPlayers[2]}
              place={3}
              size="large"
              animation="slide-right"
            />
          )}
        </div>
      </div>

      {/* Мой результат + последнее место */}
      {(currentUser || lastPlayer) && (
        <div className="mt-10 grid grid-cols-1 md:grid-cols-2 gap-6 max-w-4xl w-full px-6">
          {currentUser && currentUserIndex > 2 && (
            <InfoCard
              title="МОЙ РЕЗУЛЬТАТ"
              player={currentUser}
              icon={
                <i className="fas fa-user-shield text-blue-400 text-2xl"></i>
              }
              place={currentUserIndex + 1}
            />
          )}
          {lastPlayer && (
            <InfoCard
              title="ПОСЛЕДНЕЕ МЕСТО"
              player={lastPlayer}
              icon={<i className="fas fa-flag text-red-400 text-2xl"></i>}
              place={players.length}
            />
          )}
        </div>
      )}

      {/* Количество зарегистрированных игроков */}
      <div className="mt-12 flex justify-center items-center">
        <div className="registered-users flex items-center gap-2 py-2 px-4 shadow-lg rounded-full">
          <i className="fas fa-users text-xl text-white"></i>
          <p className="text-sm text-gray-200">
            ВСЕГО ПОЛЬЗОВАТЕЛЕЙ:{" "}
            <span className="font-semibold">{players.length}</span>
          </p>
        </div>
      </div>

      {/* Назад */}
      <button className="back-button" onClick={() => navigate(-1)}>
        ←
      </button>
    </div>
  );
}

// Компонент карточки игрока на подиуме
function PlayerCard({ player, place, size, animation }) {
  // Классы для цвета значка места
  const placeClass =
    place === 1 ? "place-1" : place === 2 ? "place-2" : "place-3";
  const sizeClass = size === "large" ? "large" : "small";
  return (
    <div
      className={`player-card ${sizeClass} animate-${animation}`}
    >
      <div className={`place-indicator ${placeClass}`}>
        {place === 1
          ? "🥇"
          : place === 2
          ? "🥈"
          : "🥉"}
      </div>
      <div className="mt-2 text-center">
        <h3 className="player-name font-semibold">
          {player.users?.username || "Нет имени"}
        </h3>
        {player.is_prime ? (
          <img
            src={PrimeIcon}
            alt="Prime"
            className="w-6 h-6 inline-block mt-1"
          />
        ) : (
          <p className="text-red-500 mt-1">NO PRIME</p>
        )}
        <p className="text-sm mt-1">PC: {player.plum_count}</p>
        <p className="text-sm">Уровень: {player.level}</p>
      </div>
    </div>
  );
}

// Компонент карточки информации
function InfoCard({ title, player, icon, place }) {
  return (
    <div className="info-card flex flex-col items-center">
      <div className="flex items-center gap-3 mb-3">
        {icon}
        <h3 className="text-lg font-bold">{title}</h3>
      </div>
      <div className="text-center">
        <div className="font-semibold text-blue-300">🏅 {place} место</div>
        <h4 className="text-white font-medium mt-1">
          {player.users?.username || "Нет имени"}
        </h4>
        {player.is_prime ? (
          <img
            src={PrimeIcon}
            alt="Prime"
            className="w-5 h-5 inline-block mt-1"
          />
        ) : (
          <p className="text-red-500 mt-1">NO PRIME</p>
        )}
        <p className="text-sm text-gray-300 mt-1">PC: {player.plum_count}</p>
        <p className="text-sm text-gray-300">Уровень: {player.level}</p>
      </div>
    </div>
  );
}

export default Leaderboard;
