// src/Auth.js
import React, { useState } from "react";
import { supabase } from "./supabaseClient";

const EyeIcon = ({ visible }) => {
  if (visible) {
    return (
      <svg
        xmlns="http://www.w3.org/2000/svg"
        className="h-6 w-6 text-gray-400"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor"
      >
        {/* Eye-off иконка */}
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth="2"
          d="M13.875 18.825A10.05 10.05 0 0112 19c-4.477 0-8.268-2.943-9.542-7a10.05 10.05 0 012.197-3.288M4.228 4.228l15.544 15.544"
        />
      </svg>
    );
  } else {
    return (
      <svg
        xmlns="http://www.w3.org/2000/svg"
        className="h-6 w-6 text-gray-400"
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor"
      >
        {/* Eye icon */}
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth="2"
          d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
        />
        <path
          strokeLinecap="round"
          strokeLinejoin="round"
          strokeWidth="2"
          d="M2.458 12C3.732 7.943 7.523 5 12 5c4.477 0 8.268 2.943 9.542 7-1.274 4.057-5.065 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"
        />
      </svg>
    );
  }
};

const Auth = () => {
  const [isSignUp, setIsSignUp] = useState(false);
  const [loginInput, setLoginInput] = useState(""); // Email или Username
  const [email, setEmail] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState(""); // Подтверждение пароля
  const [message, setMessage] = useState("");

  // Состояния для показа/скрытия пароля
  const [showPassword, setShowPassword] = useState(false);
  const [showSignUpPassword, setShowSignUpPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  // Регистрация пользователя
  const handleSignUp = async () => {
    if (password !== confirmPassword) {
      setMessage("Ошибка: Пароли не совпадают.");
      return;
    }

    const { data, error } = await supabase.auth.signUp({ email, password });

    if (error) {
      setMessage(`Ошибка: ${error.message}`);
    } else {
      setMessage("Регистрация успешна! Проверьте почту для подтверждения.");

      // Проверяем, есть ли объект пользователя
      if (data && data.user) {
        const { dbError } = await supabase
          .from("users")
          .insert([{ user_id: data.user.id, username, email }]);

        if (dbError) {
          setMessage(`Ошибка при сохранении имени пользователя: ${dbError.message}`);
        }
      } else {
        setMessage("Ошибка: Пользователь не был создан.");
      }
    }
  };

  // Авторизация пользователя (по username или email)
  const handleSignIn = async () => {
    let loginField = "email"; // По умолчанию вход через email

    // Если ввели не email, ищем username в таблице
    if (!loginInput.includes("@")) {
      const { data, error } = await supabase
        .from("users")
        .select("email")
        .eq("username", loginInput)
        .single();

      if (error || !data) {
        setMessage("Ошибка: Имя пользователя не найдено.");
        return;
      }

      loginField = data.email;
    } else {
      loginField = loginInput;
    }

    // Вход в систему
    const { error } = await supabase.auth.signInWithPassword({
      email: loginField,
      password,
    });

    if (error) {
      setMessage(`Ошибка: ${error.message}`);
    } else {
      setMessage("Вы успешно вошли!");
    }
  };

  return (
    <div className="flex flex-col items-center justify-center h-screen from-pink-500 via-purple-500 to-indigo-500">
      <div className="bg-black bg-opacity-60 p-8 rounded-lg shadow-xl w-80">
        <h2 className="text-3xl font-bold mb-6 text-center">
          {isSignUp ? "Регистрация Plum" : "Вход в Plum"}
        </h2>

        {/* Авторизация: сначала ввод Email или Username, затем пароль */}
        {!isSignUp && (
          <>
            <input
              type="text"
              placeholder="Email или Имя пользователя"
              value={loginInput}
              onChange={(e) => setLoginInput(e.target.value)}
              className="p-3 w-full mb-4 rounded bg-gray-800 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500"
            />
            {/* Обёртка для поля пароля с иконкой */}
            <div className="relative">
              <input
                type={showPassword ? "text" : "password"}
                placeholder="Пароль"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="p-3 w-full mb-4 rounded bg-gray-800 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500"
              />
              <div
                className="absolute right-3 top-1/2 transform -translate-y-1/2 cursor-pointer"
                onClick={() => setShowPassword(!showPassword)}
              >
                <EyeIcon visible={showPassword} />
              </div>
            </div>
          </>
        )}

        {/* Регистрация: ввод Email, Username, затем пароль и подтверждение */}
        {isSignUp && (
          <>
            <input
              type="text"
              placeholder="Имя пользователя"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="p-3 w-full mb-4 rounded bg-gray-800 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500"
            />
            <input
              type="email"
              placeholder="Email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="p-3 w-full mb-4 rounded bg-gray-800 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500"
            />
            {/* Поле пароля */}
            <div className="relative">
              <input
                type={showSignUpPassword ? "text" : "password"}
                placeholder="Пароль"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="p-3 w-full mb-4 rounded bg-gray-800 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500"
              />
              <div
                className="absolute right-3 top-1/2 transform -translate-y-1/2 cursor-pointer"
                onClick={() => setShowSignUpPassword(!showSignUpPassword)}
              >
                <EyeIcon visible={showSignUpPassword} />
              </div>
            </div>
            {/* Поле подтверждения пароля */}
            <div className="relative">
              <input
                type={showConfirmPassword ? "text" : "password"}
                placeholder="Подтвердите пароль"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                className="p-3 w-full mb-6 rounded bg-gray-800 text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-purple-500"
              />
              <div
                className="absolute right-3 top-1/2 transform -translate-y-1/2 cursor-pointer"
                onClick={() => setShowConfirmPassword(!showConfirmPassword)}
              >
                <EyeIcon visible={showConfirmPassword} />
              </div>
            </div>
          </>
        )}

        <div className="flex gap-2 justify-center">
          {isSignUp ? (
            <button
              onClick={handleSignUp}
              className="bg-green-600 hover:bg-green-700 px-4 py-2 rounded font-semibold"
            >
              Зарегистрироваться
            </button>
          ) : (
            <button
              onClick={handleSignIn}
              className="bg-blue-600 hover:bg-blue-700 px-4 py-2 rounded font-semibold"
            >
              Войти
            </button>
          )}
        </div>

        {message && <p className="mt-4 text-center text-sm">{message}</p>}

        <div className="mt-6 text-center">
          <span
            className="cursor-pointer underline hover:text-gray-300"
            onClick={() => setIsSignUp(!isSignUp)}
          >
            {isSignUp
              ? "Уже есть аккаунт? Войти"
              : "Нет аккаунта? Зарегистрироваться"}
          </span>
        </div>
      </div>
    </div>
  );
};

export default Auth;
