import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { supabase } from "./supabaseClient";

// Импорт иконок вибрации
import vibrationOnIcon from "./assets/icons/vibration-on.png";
import vibrationOffIcon from "./assets/icons/vibration-off.png";

const Settings = () => {
  const navigate = useNavigate();
  
  // Состояние вибрации (загружается по умолчанию как true)
  const [isVibrationEnabled, setIsVibrationEnabled] = useState(true);

  // Переключение состояния вибрации и сохранение в localStorage
  const toggleVibration = () => {
    const newState = !isVibrationEnabled;
    setIsVibrationEnabled(newState);
    localStorage.setItem("vibrationEnabled", JSON.stringify(newState));
  };

  // Функция выхода из учётной записи через Supabase
  const handleLogout = async () => {
    try {
      const { error } = await supabase.auth.signOut();
      if (error) {
        console.error("Ошибка при выходе:", error);
      } else {
        navigate("/");
      }
    } catch (err) {
      console.error("Ошибка при выходе:", err);
    }
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-opacity-95 p-6 relative">
      <div className="bg-opacity-80 backdrop-blur-lg rounded-2xl shadow-2xl p-8 w-full max-w-md">
        <h1 className="text-4xl font-extrabold text-yellow-400 text-center mb-8">
          Настройки
        </h1>
        <button
          onClick={() => navigate("/")}
          className="w-full py-3 px-4 bg-blue-600 hover:bg-blue-700 rounded-lg text-white font-bold shadow-lg transition duration-200 mb-4"
        >
          Вернуться в игру
        </button>
        <button
          onClick={handleLogout}
          className="w-full py-3 px-4 bg-gray-600 hover:bg-gray-700 rounded-lg text-white font-bold shadow-lg transition duration-200"
        >
          Выйти из учётной записи
        </button>
      </div>

      {/* Нижняя панель с переключателем вибрации */}
      <div className="flex items-center justify-center mt-8">
        <button onClick={toggleVibration} className="focus:outline-none">
          <img
            src={isVibrationEnabled ? vibrationOnIcon : vibrationOffIcon}
            alt="Переключить вибрацию"
            className="w-10 h-10"
          />
        </button>
        <span className="ml-3 text-white font-semibold">Вибрация</span>
      </div>
    </div>
  );
};

export default Settings;
